body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.head {
    display: flex;
    flex-direction: row;
}
.logoHead {
    font-family: 'Prompt', sans-serif;
    font-size: 36px;
    color: #34558B;
}
.navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.navbar a [class^="navbar"] {
    font-family: 'Prompt', sans-serif;
    color: #273773;
    font-size: 18px;
    padding: 15px;
}

.bg {
    position: relative;
    width: 100%;
    height: auto;
}
.textboxHome {
    /* Box */
    height: 10rem;
    width: 25rem;
    position: absolute;
    left: 35%;
    top: 40%;
    /* bg */
    background-color: #273773;
    opacity: 0.5;
    filter: Alpha(opacity=55); 
    z-index: 1;
}
.textHome1 {
    /* box */
    height: 10rem;
    width: 25rem;
    position: absolute;
    left: 35%;
    top: 48%;
    z-index: 2;
    /* font */
    color: #ffffff;
    font-family: 'Prompt', sans-serif;
    font-weight: 400;
    font-size: 24px;
    text-align: center;
}
.textHome2 {
    /* box */
    height: 10rem;
    width: 25rem;
    position: absolute;
    left: 35%;
    top: 50%;
    z-index: 2;
    /* font */
    color: #ffffff;
    font-family: 'Prompt', sans-serif;
    font-weight: 400;
    font-size: 24px;
    text-align: center;
}
.textHome3 {
    /* box */
    height: 10rem;
    width: 25rem;
    position: absolute;
    left: 35%;
    top: 50%;
    z-index: 2;
    /* font */
    color: #ffffff;
    font-family: 'Prompt', sans-serif;
    font-weight: 400;
    font-size: 24px;
    text-align: center;
}

.footer {
    background-color: #243564;
    padding-bottom: 20px;
}
.textHeadFooter {
    color: #ffffff;
    font-family: 'Prompt', sans-serif;
    font-weight: 300;
    font-size: 15px;
}
.logoFooter {
    display: flex;
    justify-content: space-between;
}
.additionFooter {
    text-align: right;
}
